.rotate-center {
	-webkit-animation: rotate-center 3s ease-in-out 1.5s infinite both;
	        animation: rotate-center 3s ease-in-out 1.5s infinite both;
}
/* ----------------------------------------------
 * Generated by Animista on 2019-4-4 20:47:21
 * w: http://animista.net, t: @cssanimista
 * ---------------------------------------------- */

@-webkit-keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
  text-align: left;
}

.header-section{
	color:white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.header-section img{
	margin: 20px;
}

.status-section{
	color:white;
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
}

.status-loading{
	background-color: blueviolet;
}

.status-bad{
	background-color: #aa171e;
}

.status-denied{
	background-color: #d31a22;
}

.card-holder{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
}

.card-section{
	background-color: #2c0d0b;
	background-image: linear-gradient(180deg, #2c0d0b 50%, #6c1d0f 100%);
	margin: 10px;
	
	border-radius: 5%;
	padding: 10px;
	color: white;
	width: 320px;
	border: 5px solid #662829;
}

.card-section img:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.card-section img{
	width: 300px;
	height: 300px;
	border-radius: 25%;
	border: 5px solid #662829;
	transition: all 1s;
}

.card-image{
	text-align: center;
	margin: 5px;
}

a{
	text-decoration: none;
	color:white;
}


.count-status-0{
	text-align: center;
	color:white;
}

.count-status{
	text-align: center;
	color:white;
}

i{
	margin-right: 10px;
	font-size: 1.2rem;
}

.card-rating{
	margin: 10px 0 10px 0;
	text-align: center;
	color:yellow;
}

.card-content{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

